<template>
<example-all :my-list="menu">

</example-all>
</template>

<script>
import {onMounted, reactive} from "vue";
import EndPoints from "@/service/EndPoints";
import ExampleAll from "@/components/main/ExampleAll";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "BestGoalsGulf",
  components: {ExampleAll},
  setup(){
    let menu=reactive([])
    const total=onMounted(()=>{
      EndPoints.getGoalsGulf()
          .then((response)=>{
            if(response.data.success){
              menu.push(response.data.data.goal)
            }
          })
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      menu,
      total,
    }
  }
}
</script>

