          <div @click="getNums(2)"><button @click="tabs='StandingAgulf'" type="button" :class="{bgGreen: count===2, textWhite: count===1}" class="text-sm font-semibold text-center text-slate-500 rounded-sm p-2">میزبان</button></div>
          <div @click="getNums(3)"><button @click="tabs='StandingHgulf'" type="button" :class="{bgGreen: count===3, textWhite: count===2}" class="text-sm font-semibold text-center text-slate-500 rounded-sm p-2">مهمان</button></div>
