<script>
import{defineAsyncComponent} from "vue";
const AllStandingGulf=defineAsyncComponent(()=>import('@/components/main/PersianGulf/allStandingGulf'))
const StandingAgulf=defineAsyncComponent(()=>import('@/components/main/PersianGulf/StandingAgulf'))
const StandingHgulf=defineAsyncComponent(()=>import('@/components/main/PersianGulf/StandingHgulf'))

import{ref} from 'vue'
export default {
  name: "finalGulfs",
  components: {StandingHgulf, StandingAgulf, AllStandingGulf},
  setup(){
    const sizeText=ref(10)
    const heightBox=ref(600)
    const tabs=ref('AllStandingGulfs')
