          <div  @click="bestGoals(2)"><button @click="tabs='AssistGoalsGulf'" type="button" class="px-3 py-1 outline-none btn  text-white  rounded-sm text-sm"  :class="{black: count===2  }">پاس گل</button></div>
          <div  @click="bestGoals(3)"><button @click="tabs='TotalPersianGulfs'" type="button" class="px-3 py-1 outline-none btn  text-white  rounded-sm text-sm"  :class="{black: count===3 }">مجموع</button></div>
